import global from "../../utils/global.js";
import babyApi from "../../service/baby";
import city from "../../dataDict/city.js"

export default {
	data() {
		return {
			backUrl:"",
			areaList: city,
			currentDate: new Date().getTime(),
			maxDate: new Date().getTime(),
			formatter(type, value) {
			  if (type === 'year') {
				return `${value}年`;
			  } else if (type === 'month') {
				return `${value}月`;
			  }
			  return value;
			},
			type: 1,
			boyTyp: false,
			girlTyp: false,
			birthTyp: false,
			localTyp: false,
			show: false,
			sexname: '',
			sex: '',
			birthday: '请选择宝宝出生日期',
			local: '请选择宝宝所在地',
			"cityCode": "",
			"cityName": "",
			"countyCode": "",
			"countyName": "",
			"provinceCode": "",
			"provinceName": "",
			
			
		}
	},
	methods: {
		showPopup(type) {
			this.type = type;
			this.show = true;
		},
		onClose() {
			this.show = false;
		},
		//生日
		ondateConfirm(event) {
			var date = new Date(event.detail).toLocaleString();
			this.birthday = date.substr(0,date.indexOf(' ')).split('/').join('-');
		
			this.birthTyp = true;
			this.show = false;
		},
		//地区
		onareaConfirm(event){
			this.cityCode = event.detail.values[1].code;
			this.cityName = event.detail.values[1].name;
			this.countyCode = event.detail.values[2].code;
			this.countyName = event.detail.values[2].name;
			this.provinceCode = event.detail.values[0].code;
			this.provinceName = event.detail.values[0].name;
			this.local = this.provinceName+'/'+this.cityName+'/'+this.countyName;
			this.localTyp = true;
			this.show = false;
		},
		sexSelect(typ){
			if(typ==1){
				this.sex = 1;
				this.boyTyp = true;
				this.girlTyp = false;
			}else if(typ==2){
				this.sex = 2;
				this.boyTyp = false;
				this.girlTyp = true;
			}
		},
		savebabyBtn(){
			if(this.sex == ''){
				uni.showToast({
					icon:"none",
					title: '请选择宝宝性别'
				});
				return;
			}
			if(this.birthday == ''){
				uni.showToast({
					icon:"none",
					title: '请选择宝宝生日'
				});
				return;
			}
			if(this.provinceCode == ''){
				uni.showToast({
					icon:"none",
					title: '请选择宝宝所在地'
				});
				return;
			}
			let obj={
				"birthday": this.birthday,
				"sex": this.sex,
				"cityCode": this.cityCode,
				"cityName": this.cityName,
				"countyCode": this.countyCode,
				"countyName": this.countyName,
				"provinceCode": this.provinceCode,
				"provinceName": this.provinceName,
			};
			babyApi.babySave(obj).then(res=>{
				if(res.code == "0000"){
					global.setBabyId(res.data.id);
					global.setBabyBirthday(res.data.birthday);
					if(this.backUrl!=''){
						uni.redirectTo({
							url: this.backUrl
						});
					}else{
						uni.switchTab({
							url: '/pages/task/index'
						});
					}
					uni.showToast({
						icon:"success",
						title: '添加成功',
						duration: 2000
					});
				}else{
					uni.showToast({
						icon:"none",
						title: res.responseMsg
					});
				}
			});
		}
	},
	onLoad(option){
		if(option.backpath){
			this.backUrl = option.backpath;
		}
	}
}
